<script>
import { mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      timerValues: [10, 15, 20, 25, 30],
    };
  },
  computed: {
    ...mapState({
      timerLength: 'timerLength',
      filterOutReplies: 'filterOutReplies',
      playing: 'playing',
    }),
    timerLengthFormatted() {
      return `${this.timerLength}ms`;
    },
    timerLengthFormattedFull() {
      return `${this.timerLength * 10}ms`;
    },
    timerLengthSeconds() {
      return this.timerLength / 1000;
    },
    autoPlayTitle() {
      return this.playing ? 'Stop autoplay' : 'Start autoplay';
    },
  },
  methods: {
    ...mapActions({
      toggleFilterOutReplies: 'toggleFilterOutReplies',
      updateTimerLength: 'updateTimerLength',
    }),
    changeTimer() {
      const timerValue = this.getNextTimerValue();
      this.updateTimerLength(timerValue * 1000);
      this.setCssVariable();
      this.$emit('update-input');
    },
    getNextTimerValue() {
      if (this.timerValues && this.timerValues.length) {
        const index = this.timerValues.indexOf(this.timerLengthSeconds);

        return this.timerValues.length > index + 1 ? this.timerValues[index + 1] : this.timerValues[0];
      }

      return null;
    },
    autoPlay() {
      this.$emit('autoPlay');
    },
    getTweet() {
      this.$emit('getTweet');
    },
    setCssVariable() {
      document.documentElement.style.setProperty('--timer-length', this.timerLengthFormatted);
      document.documentElement.style.setProperty('--timer-length-full', this.timerLengthFormattedFull);
    },
  },
  mounted() {
    this.setCssVariable();
  },
};
</script>
