import Vue from 'vue';
import Vuex from 'vuex';
import { forEach, sample } from 'lodash';

// eslint-disable-next-line
import data from '@/data/pedro.js';

Vue.use(Vuex);

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

function startsWith(str, word) {
  return str.lastIndexOf(word, 0) === 0;
}

export default new Vuex.Store({
  state: {
    tweets: [],
    pureTweets: [],
    arrangedWords: [],
    randomWords: [],
    rawData: data,
    randomTweet: null,
    playing: true,
    filterOutReplies: false,
    timerLength: 15000,
  },
  getters: {
    getRandomTweet(st) {
      let { randomTweet } = st;

      if (st.filterOutReplies && randomTweet) {
        do {
          randomTweet = sample(st.pureTweets);
        }
        while (startsWith(randomTweet, '@'));
      }

      return randomTweet;
    },
    getArrangedWords: st => (amount) => {
      if (st.arrangedWords.length) {
        const value = st.arrangedWords;
        const randomItem = getRandomInt(value.length);

        return value.slice(randomItem, randomItem + parseInt(amount, 10));
      }

      return null;
    },
  },
  mutations: {
    getData(st) {
      if (data) {
        forEach(data, (tweet) => {
          st.tweets.push(tweet);
          st.pureTweets.push(tweet.text);
        });

        // eslint-disable-next-line no-param-reassign
        st.arrangedWords = st.pureTweets.join(' ');
        // eslint-disable-next-line no-param-reassign
        st.randomTweet = sample(st.pureTweets);
        // st.randomWords = shuffle([...st.arrangedWords]);
      }
    },
    updateRandomTweet(st) {
      // eslint-disable-next-line no-param-reassign
      st.randomTweet = sample(st.pureTweets);
    },
    updateAutoplay(st) {
      // eslint-disable-next-line no-param-reassign
      st.playing = !st.playing;
    },
    toggleFilterOutReplies(st) {
      // eslint-disable-next-line no-param-reassign
      st.filterOutReplies = !st.filterOutReplies;
    },
    updateTimerLength(st, value) {
      // eslint-disable-next-line no-param-reassign
      st.timerLength = value;
    },

  },
  actions: {
    getData(context) {
      context.commit('getData');
    },
    updateRandomTweet(context) {
      context.commit('updateRandomTweet');
    },
    updateAutoplay(context) {
      context.commit('updateAutoplay');
    },
    toggleFilterOutReplies(context) {
      context.commit('toggleFilterOutReplies');
    },
    updateTimerLength(context, value) {
      if (value) {
        context.commit('updateTimerLength', value);
      }
    },
  },
});
