<template>
  <div
    :class="classes"
    v-on:keyup.space.prevent="spacebar"
  >
    <Navigation
      @autoPlay="autoPlay()"
      @getTweet="getTweet()"
    />
    <figure class="pedro-inspiration__block">
      <img
        :src="backgroundImage"
        srcset=""
        class="pedro-inspiration__image"
        load="lazy"
      >
      <figcaption class="pedro-inspiration__text">
        <span v-html="cleanedTweet"></span>
      </figcaption>
    </figure>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex';
import Navigation from '@/components/navigation/Navigation.vue';

export default {
  components: {
    Navigation,
  },
  data() {
    return {
      timer: null,
      backgroundReset: 0,
      backgroundImage: 'https://source.unsplash.com/collection/397119',
    };
  },
  computed: {
    ...mapState({
      playing: 'playing',
      timerLength: 'timerLength',
    }),
    ...mapGetters({
      randomTweet: 'getRandomTweet',
    }),
    classes() {
      return [
        'pedro-inspiration',
        'pedro-player',
        {
          'pedro-player--playing': this.playing,
        },
      ];
    },
    cleanedTweet() {
      return this.randomTweet
        ? this.randomTweet.replace(
          'GRTZ PEDRO',
          '<strong class="pedro-grtz">GRTZ PEDRO</strong>',
        )
        : null;
    },
  },
  methods: {
    ...mapActions({
      getData: 'getData',
      updateRandomTweet: 'updateRandomTweet',
      autoPlay: 'updateAutoplay',
    }),
    getTweet() {
      this.updateRandomTweet();

      this.backgroundReset += 1;

      if (this.backgroundReset === 8) {
        this.showNewBackground();
      }

      if (this.backgroundReset > 10) {
        this.showNewBackground();
      }
    },
    showNewBackground() {
      this.backgroundReset = 0;
      this.backgroundImage = `${this.backgroundImage}?1`;
    },
    spacebar() {
      console.log('space');
    },
  },
  mounted() {
    this.getData();

    if (this.playing) {
      this.timer = window.setInterval(this.getTweet, this.timerLength);
    } else if (this.timer) {
      window.clearInterval(this.timer);
    }
  },
  watch: {
    playing(playState) {
      if (playState) {
        this.timer = window.setInterval(this.getTweet, this.timerLength);
      } else if (this.timer) {
        window.clearInterval(this.timer);
      }
    },
  },
};
</script>
